body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  /* background: url("/Images/pexels-lukas-616401.jpg") no-repeat; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  position: relative;
}

.App-link {
  color: #61dafb;
}

.Homepage-background {
  position: absolute;
  top: 0;
  left: 240px;
  height: auto;
  max-width: 100%;
  opacity: 0.6;
  z-index: -1;
}

.ClippedDrawer {
  background-color: white;
  border-bottom: 2px solid rgba(0, 0, 0, 0.125);
}

.nav-link.active {
  font-weight: bold;
  color: black;
}

.Home {
  height: calc(100vh - 150px);
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  text-align: center;
  margin: auto;
}

.GetRecipeByIngredient input:focus {
  box-shadow: none;
  border-color: #ced4da;
}

.Form {
  padding: 20px;
}

.Button {
  padding: 20px;
  margin: 20px;
}

.TextField {
  padding: 20px;
  margin: 20px;
}

